a {
    text-decoration: none;
    color:black;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif

  }


  
#contentBox {
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    overflow-wrap: break-word;
    overflow: auto;
    position: absolute;
    top: 8px;
    left: 30%;
    background-color:whitesmoke;
    width: 66%;
    height: 90%;
    padding-left: 2%;
    border: 2px solid silver;
    border-radius: 10px;

}




#navBar {
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    border: 2px solid silver;
    border-radius: 10px;
    left: 0%;
    top: 0%;
    width: 25%;
    height: 100%;
    border-style:outset;
    padding-left: 2%;
    padding-bottom: 2%;
}

#hitButton {
    background-color: green;

    
}


#standButton {
    background-color: red;
